








































































    declare global {
        interface Window{ PrismicJS: any, PrismicDOM: any }
    }	


	import { Component, Vue, Watch} from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';

	Component.registerHooks(['beforeRouteEnter']);
		
	@Component
	export default class VideoStream extends Vue {
		prismicDOM = window.PrismicDOM; 
		documents: any = [];	
        overlay = 'linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),';
        about: any = {}

        @Watch('$route', {deep: true}) _changeRoute() {
            this._getPage();
        }

		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getPage(); });
		}

		async _getPage() {
			let api = await _Prismic();
			let response = await api.query(window.PrismicJS.Predicates.at('document.type', 'about'),
				{fetch:['about.' + this.$route.params.id, 'about.middle_image'] }
			);
            this.about = response.results[0].data;
			this.documents = response.results[0].data[this.$route.params.id];
		}

        backgroundImage(url) {
            return this.overlay + 'url("'+ url +'")';
        }

}
